import logo from './logo.svg';
import './App.css';
import { Component } from 'react';

import { CardList } from './components/card-list/card-list.component';
import { SearchBox } from './components/search-box/search-box.component';

class App extends Component {
  constructor() {
    super();
    this.state = {  
      persons: [],
      searchField: '' 
    };

    // this.handleChange = this.handleChange.bind(this);
  }

  async componentDidMount() {
    const response = await fetch('https://jsonplaceholder.typicode.com/users');
    const persons = await response.json();
    this.setState({ persons: persons}); 
  }

  //wont work unless you explicitly bind 
  // handleChange(e) {
  //   this.setState({ searchField: e.target.value });
  // }

  handleChange = (e)  => {
    this.setState({ searchField: e.target.value });
  }

  render() {

    const { persons, searchField } = this.state; 
    const filteredPersons = persons.filter(person => person.name.toLowerCase().includes(searchField.toLowerCase()))

    return (
      <div className="App">
        <h1>Face Cards</h1>
        <SearchBox
        placeholder="search persons"
        handleChange={this.handleChange}
        />
        <CardList persons={filteredPersons} />
      </div>
    );
  }

}

export default App;
